<template>
    <div id="article">
      <div v-html="article.body" class="markdown-body" v-marked></div>
    </div>
</template>
<script>
import { getArticleByNumber } from '@/api';
import { convertBlogArticle } from '@/utils/helper'

export default {
  data() {
    return {
      article:{},
      isProgress: false
    }
  },
  created(){
    this.getArticles()
  },
  methods: {
    getArticles: function(){
      this.isProgress = true
      getArticleByNumber('guanpengchn.github.io',this.$route.params.id)
        .then(article => {
          this.article = convertBlogArticle(article) 
          this.isProgress = false
        })
    }
  }
};
</script>
<style scoped>
@import '../styles/app.css';
@import '../styles/base.css';
@import '../styles/atom-one-dark.css';
</style>


